  border-top: 3px solid #95a5a6;
  border-bottom: 3px solid #95a5a6; }
  .experience:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
    cursor: pointer; }

li {
  margin: .5vmin; }

ul {
