ul:last-child {
  margin-bottom: 0px; }

.underline, .overline {
  display: inline-block;
  position: relative;
  padding-bottom: 4px; }

.underline::after {
  position: absolute;
  content: '';
  bottom: 3px;
  left: 0px;
  height: .25vmin;
  width: 0%;
  background-color: #F5F5F5;
  -webkit-animation: grow 1.5s ease-in-out 1.5s both;
          animation: grow 1.5s ease-in-out 1.5s both; }

.overline::before {
  position: absolute;
  content: '';
  top: 0px;
  right: 0px;
  height: .25vmin;
  width: 0%;
  background-color: #F5F5F5;
  -webkit-animation: grow 1.5s ease-in-out 1.5s both;
          animation: grow 1.5s ease-in-out 1.5s both; }

@-webkit-keyframes grow {
  to {
    width: 100%; } }

@keyframes grow {
  to {
    width: 100%; } }

.extra-enter {
  -webkit-transform: scaleY(0);
          transform: scaleY(0); }

.extra-enter-active {
  -webkit-transform-origin: top;
          transform-origin: top;
  transition: -webkit-transform .25s ease;
  transition: transform .25s ease;
  transition: transform .25s ease, -webkit-transform .25s ease;
  -webkit-transform: scaleY(1);
          transform: scaleY(1); }

.extra-exit {
  -webkit-transform: scale(1);
          transform: scale(1); }

.extra-exit-active {
  -webkit-transform: scaleY(0);
          transform: scaleY(0);
  -webkit-transform-origin: top;
          transform-origin: top;
  transition: -webkit-transform .25s ease;
  transition: transform .25s ease;
  transition: transform .25s ease, -webkit-transform .25s ease; }

.upbeat {
  -webkit-animation: top-blur-in 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.5s both;
          animation: top-blur-in 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.5s both; }

