  -webkit-animation: top-blur-in 0.75s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.5s both;
          animation: top-blur-in 0.75s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.5s both; }

